(function(){
    "use strict";

    angular
        .module("starterApp")
        .config(md);

    md.$inject = ["$mdThemingProvider", "$mdIconProvider"];

    function md($mdThemingProvider, $mdIconProvider){

        // note: if this app became larger then each feature area should take on the responsibility of configuring
        // material design with icon's for its own area

        $mdIconProvider
            .defaultIconSet("demoApp/images/avatars.svg", 128)
            .iconSet("nav", "demoApp/images/svg-sprite-navigation.svg", 24)
            .icon("menu", "demoApp/images/menu.svg", 24)
            .icon("share", "demoApp/images/share.svg", 24)
            .icon("google_plus", "demoApp/images/google_plus.svg", 512)
            .icon("hangouts", "demoApp/images/hangouts.svg", 512)
            .icon("twitter", "demoApp/images/twitter.svg", 512)
            .icon("phone", "demoApp/images/phone.svg", 512);

        $mdThemingProvider.theme("default")
            .primaryPalette("brown")
            .accentPalette("red");
    }
})();