(function(module) {
try {
  module = angular.module('shared');
} catch (e) {
  module = angular.module('shared', []);
}
module.run(['$templateCache', function($templateCache) {
  $templateCache.put('demoApp/users/view/contactSheet.html',
    '<md-bottom-sheet class="md-list md-has-header"><md-subheader>Contact <span class="name">{{ cp.user.name }}</span>:</md-subheader><md-list><md-item ng-repeat="item in cp.actions"><md-button ng-click="cp.submitContact(item)" id="item_{{$index}}"><md-icon md-svg-icon="{{ item.iconUrl }}"></md-icon>{{item.name}}</md-button></md-item></md-list></md-bottom-sheet>');
}]);
})();
